<template>
  <div class="d-flex">
    <SideBar />
    <div class="flex-grow-1 p-3">
      <div v-if="items" class="d-flex">
        <div class="left-content">
          <p>: {{ items.res_id }}</p>
          <p>: {{ items.res_name }}</p>
          <p>: {{ items.check_in }}</p>
          <p>: {{ items.check_out }}</p>
          <p> : {{ items.content }}</p>
          <p>: {{ items.rating }}</p>
          <p>: {{ items.created_date }}</p>
          <p>: {{ items.modified_date }}</p>
          <p>: {{ items.place_nm }}</p>
          <p>: {{ items.room_name }}</p>
          <p>: {{ items.reply_exist }}</p>

          <div v-if="items.picture_sources && items.picture_sources.length > 0">
            <div
              v-for="(item, index) in items.picture_sources"
              :key="index"
              class="image-container"
            >
              <span
                class="img"
                :style="{ backgroundImage: `url(${item})` }"
              ></span>
            </div>
          </div>

          <div>
            <button @click="deletePeviewTouchUpInside"></button>
          </div>
        </div>
        
        <div class="right-content">
          <div v-if="items.reply_exist === 'N'" class="comment-container">
            <p>  </p>
            <textarea v-model="reply_comment"></textarea>
            <br />
            <button @click="insertReplyTouchUpInside"> </button>
          </div>
          <div v-else class="comment-container">
            <p> :</p>
            <p>{{ items.reply_content }}</p>
            <button class="delete-button" @click="deleteReplyTouchUpInside"> </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/axios'
import SideBar from '@/views/SideBar.vue'

export default {
  name: 'PartnerRevieweDetail',
  components: {
    SideBar,
  },

  data() {
    return {
      items: null,
      reply_comment: '',
    }
  },

  computed: {
    reviewId() {
      return this.$route.params.review_id
    },
  },

  created() {
    this.fetchPlaceInfo(this.reviewId)
  },

  methods: {
    fetchPlaceInfo(reviewId) {
      axios
        .get('/api/partner/reviewDetail', {
          params: {
            review_id: reviewId,
          },
        })
        .then((res) => {
          this.items = res.data
          console.log(res.data)
        })
        .catch((err) => {
          console.error(err)
        })
    },
    insertReplyTouchUpInside() {
      axios
        .post('/api/partner/replyInsert', {
          review_id: this.reviewId,
          res_id: this.items.res_id,
          reply_content: this.reply_comment,
          status: 'active',
        })
        .then(() => {
          alert(' .')
          window.location.href = 'http://localhost:8090/partner/reviewList'
        })
        .catch((err) => {
          alert('  .')
          console.error(err)
        })
    },
    deletePeviewTouchUpInside() {
      axios
        .get('/api/partner/reviewDelete', {
          params: {
            review_id: this.reviewId,
          },
        })
        .then(() => {
          alert(' .')
          window.location.href = 'http://localhost:8090/partner/reviewList'
        })
        .catch((err) => {
          alert('. ' + err)
          console.error(err)
        })
    },
    deleteReplyTouchUpInside() {
      axios
        .get('/api/partner/replyDelete', {
          params: {
            review_id: this.reviewId,
          },
        })
        .then(() => {
          alert(' .')
          window.location.href = 'http://localhost:8090/partner/reviewList'
        })
        .catch((err) => {
          alert('. ' + err)
          console.error(err)
        })
    },
  },
}
</script>

<style scoped>
.img {
  display: inline-block;
  width: 150px; /*     */
  height: 150px; /*     */
  background-size: cover;
  background-position: center;
  border-radius: 20%;
}

.image-container {
  position: relative;
  display: inline-block;
  margin: 10px;
}

.comment-container {
  position: relative; /*           */
  border: 1px solid #ccc;
  padding: 10px;
  height: 50vh; /*     */
  width: 33vw; /*   3 1  */
  box-sizing: border-box; /*       */
}

textarea {
  width: 100%; /* textarea  100%      */
  height: calc(100% - 30px); /*  100% ,       */
  resize: vertical; /* textarea       */
  margin-bottom: 10px; /*    */
}

.delete-button {
  position: absolute; /*         */
  bottom: 10px; /*    10px   */
  right: 10px; /*    10px   */
}

.left-content {
  flex: 1; /*      */
  padding-right: 10px; /*    */
}

.right-content {
  flex: 1; /*      */
  padding-left: 10px; /*    */
}
</style>