<template>
  <div class="delete-account-container">
    <h4> </h4>
    <p>
         ,  ,      .
          7   .
    </p>
    <input type="password" v-model="rawPassword" placeholder=" ">
    <button @click="checkPassword"></button>

    <div v-if="showPopup">
      <div class="popup-container">
          <div class="popup">
            <h2></h2>
            <p>      .</p>
            <p> ?</p>
            <button @click="confirmDelete"></button>
            <button @click="closePopup"></button>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/axios';
import store from "@/scripts/store";

export default {
  name: 'UserDelete',
  data() {
    return {
      rawPassword: '',
      showPopup: false,
      user_id: null
    };
  },
  mounted() {
    this.user_id = this.$route.params.user_id;
  },
  methods: {
    async checkPassword() {
      try {
        const response = await axios.post(`/api/user/check-password`, {
          user_id: this.user_id,
          user_pwd: this.rawPassword
        });

        if (response.data) {
          this.showPopup = true;
        } else {
          alert('  .');
        }
      } catch (error) {
        console.error('Error checking password:', error);
      }
    },
    async confirmDelete() {
      try {
        await axios.post(`/api/user/delete`, {
          user_id: this.user_id,
          user_pwd: this.rawPassword
        });

        alert('  .');
        store.commit('clearAccount');
        sessionStorage.removeItem("user_id");
        sessionStorage.removeItem("token");
        this.token = null;
        this.$router.push('/');
      } catch (error) {
        console.error('Error deleting account:', error);
      }
    },
    closePopup() {
      this.showPopup = false;
    }
  }
};
</script>

<style scoped>
.delete-account-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

input[type="password"] {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 4px;
  margin-right: 10px;
}

button:nth-of-type(2) {
  background-color: #dc3545;
}

.popup-container {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 100%;
}

.popup button {
  background-color: #dc3545;
}
</style>